class UtilFN {
  constructor () {}

  // 获取日期 年月日 时分秒
  getDate () {
    let timeArr = []
    let timer = new Date()
    
    timeArr.push(timer.getFullYear())
    timeArr.push(timer.getMonth() + 1)
    timeArr.push(timer.getDate())
    timeArr.push(timer.getHours())
    timeArr.push(timer.getMinutes())
    timeArr.push(timer.getSeconds())

    return timeArr
  }
}

export default new UtilFN()


